.layout {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    min-height: 400px;
    min-width: 300px;

    background: linear-gradient(-45deg, #6b0e0e,#40233b, #410f3a, #0f0f49);
    background-size: 400% 400%;
    animation: mainBackground 20s ease infinite;
    overflow: hidden;

    @media (min-width:800px) {
        flex-direction: row;
    }
}

@keyframes mainBackground 
{
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}